function Cart() {
  const {
    isEmpty, totalUniqueItems, items, updateItemQuantity, removeItem, emptyCart, totalItems, cartTotal
  } = useCart();

  if (isEmpty) return <p>Your cart is empty</p>;

  return (
    <div>
      <h1 className="text-center">Cart ({totalUniqueItems} items)

        <button className="btn btn-danger mx-5" onClick={() => emptyCart()}> Delete All product </button>
      </h1>
      <Container>
        <Row>
          <Col md="12">
            <table className="table table-hover text-center">
              <thead>
                <tr>
                  <th>Imge</th>
                  <th>Name</th>
                  <th>Quantity</th>
                  <th>Price</th>
                  <th>Operation</th>
                </tr>
              </thead>

              <tbody>
                {items.map((item) => (
                  <tr key={item.id}>
                    <td>  <img src={item.image_url} className="img-fluid" style={{ height: "50px", width: "50px" }} />  </td>
                    <td>{item.quantity} x {item.title}</td>
                    <td>{item.quantity}</td>
                    <td>{item.price * item.quantity * 48} EG.P</td>

                    <td>
                      <button
                        className="btn btn-outline-danger"
                        onClick={() =>
                          updateItemQuantity(item.id, item.quantity - 1)
                        }
                      >
                        -
                      </button>

                      <button
                        className="btn btn-outline-success mx-2"
                        onClick={() =>
                          updateItemQuantity(item.id, item.quantity + 1)
                        }
                      >
                        +
                      </button>

                      <button
                        className="btn btn-outline-warning"
                        onClick={() => removeItem(item.id)}
                      >
                        &times;
                      </button>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>

            <h3 className="text-center">
              Total product ({totalItems}) and Total price ({cartTotal * 48}) EG.P
            </h3>
          </Col>
        </Row>
      </Container>
    </div>
  );
}

function OrderPage() {
  return (
    <CartProvider>
      <Nabar />
      <div className="container-xxl bg-white p-0">
        <div className="container-xxl py-5 bg-dark hero-header ">
          <div className="container text-center my-5 pt-5 pb-4 ">
            <h1 className="display-3 text-white mb-3 animated slideInDown"> ORders Now</h1>
            <nav aria-label="breadcrumb">
              <ol className="breadcrumb justify-content-center text-uppercase">
                <li className="breadcrumb-item"><a href="/">Home</a></li>
                <li className="breadcrumb-item text-white active" aria-current="page">Welcome To ONline Orders</li>
              </ol>
            </nav>
          </div>
        </div>
      </div>
      <div className="text-center wow fadeInUp  mt-5" data-wow-delay="0.1s">
        <h5 className="section-title ff-secondary text-center text-primary fw-normal">Food Menu</h5>
        <h1 className="mb-5">Most Popular Items</h1>
      </div>
      <showAlert/>
      <Page />
      <Footer />
      <BackToTop />

    </CartProvider>
  );
}

export default OrderPage;
