import React from 'react'
import { Col, Container, Row } from "reactstrap";
import { CartProvider, useCart } from "react-use-cart";
import Footer from '../Home/Footer';
import Nabar from '../Nabar';
import { Link } from 'react-router-dom';
import BackToTop from '../BackToTop';

function Cart() {
  const {
    isEmpty, totalUniqueItems, items, updateItemQuantity, removeItem, emptyCart, totalItems, cartTotal
  } = useCart();

  if (isEmpty) return <h1 className='text-center'><p>Your cart is empty</p>

    <Link to="/order" className='btn btn-success'>ORder Now</Link>
  </h1>;

  return (
    <div>
      <Container >
  <Row>
    <Col md={12}>
      <table className="table-cart table-hover text-center">
        <thead className="bg-dark" style={{ color: "white", fontSize: '18px', height: "50px" }}>
          <tr>
            <th>Image</th>
            <th>Name</th>
            <th>Quantity</th>
            <th>Price</th>
            <th>Operation</th>
          </tr>
        </thead>
        <tbody className="mt-5">
          {items.map((item) => (
            <tr key={item.id}>
              <td>
                <img
                  src={item.image_url}
                  className="img-fluid me-5 rounded-circle"
                  style={{ height: "53px", width: "60px" }}
                />
              </td>

              <td>{item.quantity} x {item.title}</td>
              <td>{item.quantity}</td>
              <td>{item.price * item.quantity * 48} EG.P</td>
              
              <td>
                <button
                  className="btn btn-md btn-minus rounded-circle bg-light border text-warning"
                  onClick={() => updateItemQuantity(item.id, item.quantity - 1)}
                >
                  <i className="fa fa-minus"></i>
                </button>
                <button
                  className="btn btn-md btn-plus rounded-circle bg-light border mx-2 text-success"
                  onClick={() => updateItemQuantity(item.id, item.quantity + 1)}
                >
                  <i className="fa fa-plus"></i>
                </button>
                <button
                  className="btn btn-md rounded-circle bg-light border"
                  onClick={() => removeItem(item.id)}
                >
                  <i className="fa fa-times text-danger"></i>
                </button>
                <hr className='text-dark'></hr>
              </td>
            </tr>
          ))}
        </tbody>
      </table>

      <h1 className="text-center mb-4">
        <button className="btn btn-danger mx-5 my-3" onClick={() => emptyCart()}>
          Delete All Products
        </button>
      </h1>

      {/* Summary Cart */}
      <div className="summary-cart mt-5">
        <ul className="list-unstyled d-flex justify-content-between mt-2 border border-light" style={{ maxWidth: "70%" }}>
          <li><i className="text-dark fa fa-star h4"> Order Summary </i></li>
        </ul>
        <hr />
        <ul className="list-unstyled d-flex justify-content-between mt-2 border border-light" style={{ maxWidth: "70%" }}>
          <li><i className="text-warning fa fa-star h6 h5"> Sub Total</i></li>
          <li className="text-muted text-right h6">{totalItems} </li>
        </ul>
        <ul className="list-unstyled d-flex justify-content-between mt-2 border border-light" style={{ maxWidth: "70%" }}>
          <li><i className="text-warning fa fa-star h6 h5"> Discount</i></li>
          <li className="text-muted text-right h6">200 EG.P</li>
        </ul>
        <ul className="list-unstyled d-flex justify-content-between mt-2 border border-light" style={{ maxWidth: "70%" }}>
          <li><i className="text-warning fa fa-star h5"> Tax </i></li>
          <li className="text-muted text-right h6"> 50 EG.P</li>
        </ul>
        <ul className="list-unstyled d-flex justify-content-between mt-2 border border-light" style={{ maxWidth: "70%" }}>
          <li><i className="text-warning fa fa-star h5"> Shopping Cost</i></li>
          <li className="text-muted text-right h6">Free</li>
        </ul>
        <hr />
        <ul className="list-unstyled d-flex justify-content-between mt-2 border border-light" style={{ maxWidth: "70%" }}>
          <li><i className="text-dark h3"> Grand Total</i></li>
          <li className="text-muted text-right h6">{cartTotal * 48 - (200 - 50)} EG.P</li>
        </ul>
        <hr />
        <div className="mt-5">
          <input type="text" className="border-0 border-bottom rounded me-5 py-3 mb-4" placeholder="Coupon Code" />
          <button className="btn border-outline-primary rounded-pill px-4 py-3 text-primary cart-button" type="button">
            Apply Coupon
          </button>
        </div>
      </div>

      <div className="col-12 d-flex shopping-box">
        <Link to="/checkout" className="ml-auto btn btn-danger btn-lg m-5">
          Checkout
        </Link>
      </div>
    </Col>
  </Row>
</Container>

    </div>
  );
}

function CartPage() {
  return (
    <CartProvider>
      <Nabar />
      <div className="container-xxl bg-white p-0">
        <div className="container-xxl py-5 bg-dark hero-header mb-5">
          <div className="container text-center my-5 pt-5 pb-4">
            <h1 className="display-3 text-white mb-3 animated slideInDown">YOUR ORDERS</h1>
            <nav aria-label="breadcrumb">
              <ol className="breadcrumb justify-content-center text-uppercase">
                <li className="breadcrumb-item"><a href="/">Home</a></li>
              </ol>
            </nav>
          </div>
        </div>
      </div>
      <div className="text-center wow fadeInUp  mt-5" data-wow-delay="0.1s">
        <h5 className="section-title ff-secondary text-center text-primary fw-normal">Food</h5>
        <h1 className="mb-5">Cart Orders</h1>
      </div>
      <Cart />
      <Footer />
      <BackToTop/>
    </CartProvider>
  )
}

export default CartPage;
